import Endpoint from '@/services/Endpoint'

export default {
  transactionTypes (payload) {
    return Endpoint().get('/s1/account/transactionType', {
      params: payload
    })
  },
  createTransactionType (payload) {
    return Endpoint().post('/s1/account/transactionType', payload)
  },
  modifyTransactionType (payload) {
    return Endpoint().patch('/s1/account/transactionType', payload)
  }
}
